/*
* Style Works for both Editor and Frontend
*/

.wp-block-flexguten-amazon-review-one {
	border: 1px solid #ccc;
	padding: 20px;
	.amazon-review-block {
		display: grid;
		grid-template-columns: 2fr 3fr;
		grid-gap: 20px;
		.review-image {
			height: auto;
			width: auto;
			display: grid;
			justify-items: center;
			& img {
				object-fit: cover;
			}
		}

		.star-rating {
			font-size: 25px;
			margin-top: 20px;
		}

		.rating-count {
			font-family: "Nunito", sans-serif;
		}

		.review-text {
			padding: 0px 15px;

			& h3 {
				margin: 0px;
				font-family: "Nunito", sans-serif;
				font-weight: 800;
			}

			.product-features {
				padding: 15px;
				& li {
					font-family: "Nunito", sans-serif;
					line-height: 1.3;
				}
			}
		}

		.flexguten-pricing-section {
			justify-items: end;
			display: grid;
			.flexguten-product-price {
				padding: 10px 0px;
				font-weight: 900;
				font-family: "Nunito", sans-serif;
				margin-bottom: 10px;
			}
			.flexguten-price-button .amazon-button {
				padding: 10px;
				border-radius: 5px;
				text-decoration: none;
				font-family: "Nunito", sans-serif;
				background-image: linear-gradient(
					262deg,
					#ffac30 0%,
					#ffc400 100%
				);
			}
		}
	}
}
